.title-bar {
  background-color: #313131;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  /* align-content: space-between; */
  height: 9em;
}

.App-logo {
  margin: 0;
  height: 2em;
  max-width: 70%;
  margin: auto;
}



.tab {
  border: 0 none;
  border-top: 3px dashed white;
  background-color: #313131;
  height: 0;
  border-radius: 50%;
  width: 4em;
  height: 4em;
  display: inline-block;

  margin: auto;
  margin-top: -2em;
}


