.tabs {
  color: white;
}

.line {
  border: 0 none;
  border-top: 4px dashed white;
  background: none;
  width: 100%;
  height: 0;
  -ms-flex-item-align: baseline;
      align-self: baseline;
}

.accordion__body {
  display: block;
  -webkit-animation: fadein .35s ease-in;
          animation: fadein .35s ease-in;
}

.accordion__body--hidden {
  display: none;
  opacity: 0;
  -webkit-animation: fadein .35s ease-in;
          animation: fadein .35s ease-in;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  0% {
   opacity: 0;
  }

  100% {
    opacity: 1;
  }
}



